<template>
  <div class="cart">
    <van-nav-bar title="购物车" fixed />

    <div v-if="isLogin && cartList.length > 0">
      <!-- 购物车开头 -->
      <div class="cart-title">
        <span class="all">共<i>{{cartTotal}}</i>件商品</span>
        <span class="edit" @click="isEdited = !isEdited">
          <van-icon name="edit"  />
          编辑
        </span>
      </div>

      <!-- 购物车列表 -->
      <div class="cart-list">
        <div class="cart-item" v-for="e in cartList" :key="e.goods_id">
          <van-checkbox @click="toggleChecked(e.goods_id)" :value="e.isChecked"></van-checkbox>
          <div class="show">
            <img :src="e.goods.goods_image" alt="">
          </div>
          <div class="info">
            <span class="tit text-ellipsis-2">{{e.goods.goods_name}}</span>
            <span class="bottom">
              <div class="price">¥ <span>{{e.goods.goods_price_min}}</span></div>
              <!-- 既希望保留原本的参数，又需要通过调用函数传参 =》 用箭头函数包装一层额外传参 -->
              <CountBox @input="(value)=>changeCount(value,e.goods_id,e.goods_sku_id)" :value="e.goods_num"></CountBox>
            </span>
          </div>
        </div>
      </div>

      <div class="footer-fixed">
        <div  class="all-check" @click="checkAll">
          <van-checkbox  icon-size="18" :value="isAllChecked"></van-checkbox>
          全选
        </div>

        <div class="all-total">
          <div class="price">
            <span>合计：</span>
            <span>¥ <i class="totalPrice">{{selPrice}}</i></span>
          </div>
          <div v-if="!isEdited" class="goPay" :class="{disabled:selCount === 0}" @click="goPay">结算({{selCount}})</div>
          <div v-else class="delete" :class="{disabled:selCount === 0}" @click="deleteItem">删除</div>
        </div>
      </div>
    </div>

    <div class="empty-cart" v-else>
      <img src="@/assets/empty.png" alt="">
      <div class="tips">
        您的购物车是空的, 快去逛逛吧
      </div>
      <div class="btn" @click="$router.push('/')">去逛逛</div>
    </div>

  </div>
</template>

<script>
import CountBox from '@/components/countBox.vue'
import { mapGetters, mapState } from 'vuex'
export default {
  name: 'CartPage',
  components: {
    CountBox
  },
  data () {
    return {
      isEdited: false
    }
  },
  watch: {
    isEdited (value) {
      if (value) {
        this.$store.commit('cart/checkAll', false)
      } else {
        this.$store.commit('cart/checkAll', true)
      }
    }
  },
  computed: {
    // 对仓库的数据进行一些处理后，用辅助函数拿到当前组件使用
    ...mapState('cart', ['cartList', 'cartTotal']),
    ...mapGetters('cart', ['selCount', 'selPrice', 'isAllChecked', 'selCartList']),
    isLogin () {
      return this.$store.getters.token
    }
  },
  created () {
    if (this.isLogin) {
      // 调用cart仓库里的数据 -- 仓库里调用cart请求组件，获得数据
      this.$store.dispatch('cart/getCartAction')
    }
  },
  methods: {
    // 小复选框点击变化时，修改仓库里的数据（因为isChecked属性是自己加的，所以不用传后台）
    toggleChecked (id) {
      this.$store.commit('cart/toggleChecked', id)
    },
    // 大复选框改变状态时，小复选框跟随改变
    checkAll () {
      this.$store.commit('cart/checkAll', !this.isAllChecked)
    },
    changeCount (goodsNum, goodsId, goodsSkuId) {
      // 调用vuex 的actions 对数据进行修改
      this.$store.dispatch('cart/changeCount', {
        goodsNum, goodsId, goodsSkuId
      })
    },
    async deleteItem () {
      if (this.selCount === 0) { return false }
      await this.$store.dispatch('cart/deleteAction')
      this.isEdited = false
    },
    goPay () {
      console.log(this.selCartList)
      if (this.selCount > 0) { // 有商品被选中才跳转
        this.$router.push({
          path: '/pay',
          query: {
            mode: 'cart',
            cartIds: this.selCartList.map(e => e.id).join(',')
          }
        })
      }
    }
  }
}
</script>

<style lang="less" scoped>
// 主题 padding
.cart {
  padding-top: 46px;
  padding-bottom: 100px;
  background-color: #f5f5f5;
  min-height: 100vh;
  .cart-title {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    font-size: 14px;
    .all {
      i {
        font-style: normal;
        margin: 0 2px;
        color: #fa2209;
        font-size: 16px;
      }
    }
    .edit {
      .van-icon {
        font-size: 18px;
      }
    }
  }

  .cart-item {
    margin: 0 10px 10px 10px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background-color: #ffffff;
    border-radius: 5px;

    .show img {
      width: 100px;
      height: 100px;
    }
    .info {
      width: 210px;
      padding: 10px 5px;
      font-size: 14px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .bottom {
        display: flex;
        justify-content: space-between;
        .price {
          display: flex;
          align-items: flex-end;
          color: #fa2209;
          font-size: 12px;
          span {
            font-size: 16px;
          }
        }
        .count-box {
          display: flex;
          width: 110px;
          .add,
          .minus {
            width: 30px;
            height: 30px;
            outline: none;
            border: none;
          }
          .inp {
            width: 40px;
            height: 30px;
            outline: none;
            border: none;
            background-color: #efefef;
            text-align: center;
            margin: 0 5px;
          }
        }
      }
    }
  }
}

.footer-fixed {
  position: fixed;
  left: 0;
  bottom: 50px;
  height: 50px;
  width: 100%;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;

  .all-check {
    display: flex;
    align-items: center;
    .van-checkbox {
      margin-right: 5px;
    }
  }

  .all-total {
    display: flex;
    line-height: 36px;
    .price {
      font-size: 14px;
      margin-right: 10px;
      .totalPrice {
        color: #fa2209;
        font-size: 18px;
        font-style: normal;
      }
    }

    .goPay, .delete {
      min-width: 100px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      background-color: #fa2f21;
      color: #fff;
      border-radius: 18px;
      &.disabled {
        background-color: #ff9779;
      }
    }
  }

}
.empty-cart {
  padding: 80px 30px;
  img {
    width: 140px;
    height: 92px;
    display: block;
    margin: 0 auto;
  }
  .tips {
    text-align: center;
    color: #666;
    margin: 30px;
  }
  .btn {
    width: 110px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    background-color: #fa2c20;
    border-radius: 16px;
    color: #fff;
    display: block;
    margin: 0 auto;
  }
}
</style>
