const INFO_KEY = 'shop-user-info'
const HISTORY_KEY = 'history-list'

// 获取个人信息： 有值传值，没值传默认
export const getInfo = () => {
  const defaultObj = { token: '', userId: '' }
  const res = localStorage.getItem(INFO_KEY)
  return res ? JSON.parse(res) : defaultObj
}

// 设置个人信息
export const setInfo = (obj) => {
  const content = JSON.stringify(obj)
  localStorage.setItem(INFO_KEY, content)
}

export const removeInfo = () => {
  localStorage.removeItem(INFO_KEY)
}

// 获取搜索历史
export const getHistory = () => {
  const res = localStorage.getItem(HISTORY_KEY)
  return res ? JSON.parse(res) : []
}

// 设置搜索历史
export const setHistory = (arr) => {
  localStorage.setItem(HISTORY_KEY, JSON.stringify(arr))
}
