import { getInfo, setInfo } from '@/utils/storage'

export default {
  namespaced: true,
  state () {
    return {
      //  个人权证相关
      userInfo: getInfo()
    }
  },
  mutations: {
    setUserInfo (state, obj) {
      state.userInfo = obj
      setInfo(obj)
    }
  },
  actions: {
    logout (context) {
      // 个人信息重置
      context.commit('setUserInfo', {})

      // 购物车数据重置 （跨模块进行数据操作） { root: true } 作为第三参数，作用是开启全局模式，使其访问到挂载到全局的其他模组上的数据，具体详情看vuex官网
      context.commit('cart/setCart', [], { root: true })
    }
  },
  getters: {}
}
