import Vue from 'vue'
import VueRouter from 'vue-router'

import Layout from '@/views/layout/index'
import Home from '@/views/layout/home'
import Category from '@/views/layout/category'
import Cart from '@/views/layout/cart'
import User from '@/views/layout/user'

import store from '@/store'

const Login = () => import('@/views/login/index')
const Order = () => import('@/views/order/index')
const Pay = () => import('@/views/pay/index')
const Search = () => import('@/views/search/index')
const SearchList = () => import('@/views/search/searchList')
const Address = () => import('@/views/pay/address')
const Details = () => import('@/views/details/index')

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    { path: '/login', component: Login },
    {
      path: '/layout',
      component: Layout,
      children: [
        { path: '/home', component: Home },
        { path: '/category', component: Category },
        { path: '/cart', component: Cart },
        { path: '/user', component: User }
      ]
    },
    { path: '/search', component: Search },
    { path: '/searchlist', component: SearchList },
    // 动态路由传参，为了方便传查询参数
    { path: '/details/:id', component: Details },
    { path: '/order', component: Order },
    { path: '/pay', component: Pay },
    { path: '/address', component: Address },
    { path: '/', redirect: '/home' }
  ]
})

// 定义一个数组，存放所有需要权限访问的页面
const authUrls = ['/pay', '/order']

// 全局前置导航守卫：所有路由一旦被匹配到，都会先经过这里，只有导航守卫放行，才能访问目标组件
// to到哪去，from从哪来，next执行下一步是否放行用next()  里面有参数就是进行拦截了，里面参数写跳转的页面路径
router.beforeEach((to, from, next) => {
  if (!authUrls.includes(to.path)) {
    next()
    return false
  }

  const token = store.getters.token
  if (token) {
    next()
  } else {
    next('/login')
  }
})

export default router
