<template>
  <div class="count-box">
    <button class="sub" @click="countNum(-1)">-</button>
    <input class="inp" type="text" :value="value" @change="inpChange">
    <button class="add" @click="countNum(1)">+</button>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Number,
      default: 1
    }
  },
  methods: {
    countNum (num) {
      if (num < 0 && this.value <= 1) {
        return false
      }
      this.$emit('input', this.value + num)
    },
    inpChange (e) {
      const num = +e.target.value
      if (isNaN(num) || num < 1) {
        e.target.value = this.value
        return
      }
      e.target.value = parseInt(num)
      this.$emit('input', parseInt(num))
    }
  }
}
</script>

<style lang="less" scoped>
.count-box{
    width: 110px;
    display: flex;
    .add, .sub {
        width: 30px;
        height: 30px;
        outline: none;
        border: none;
        background-color: #efefef;
    }
    .inp {
        width: 40px;
        height: 30px;
        border: none;
        outline: none;
        margin: 0 5px;
        background-color: #efefef;
        text-align: center;
    }
}
</style>
