import { getCartList, changeCount, deleteItem } from '@/api/cart'
import { Toast } from 'vant'

export default {
  namespaced: true,
  state () {
    return {
      cartList: [],
      cartTotal: ''
    }
  },
  mutations: {
    // 提供一个存储数据的mutation
    setCart (state, obj) {
      state.cartTotal = obj.cartTotal
      state.cartList = obj.list
    },
    // 小复选框 找到对应的项使其取反
    toggleChecked (state, id) {
      const item = state.cartList.find(e => e.goods_id === id)
      item.isChecked = !item.isChecked
    },
    // 大复选框 点击改变状态，让小复选框跟随改变
    checkAll (state, value) {
      state.cartList.forEach(e => { e.isChecked = value }) // 因为要用等号赋值，所以不能直接用一行的形式简写，要加上{ }
    },
    // 修改购物车商品数量的本地数据 -- 因为从后台拿数据有延迟，且商品数量上没有安全性要求，所以可以先在本地修改，优化用户体验
    changeCountStore (state, obj) {
      console.log(obj)
      state.cartList.find(e => e.goods_id === obj.goodsId).goods_num = obj.goodsNum
    }
  },
  actions: {
    async getCartAction (context) {
      const res = await getCartList()
      //   后台返回的数据中，不包含复选框是否选中，因为需要维护数据，给list的每一项手动添加 isChecked 属性
      res.data.list.forEach(e => {
        e.isChecked = false
      })
      context.commit('setCart', res.data)
    },
    async changeCount (context, obj) {
      const { goodsNum, goodsId, goodsSkuId } = obj
      // 先本地修改，修改完的数据同步到后台
      context.commit('changeCountStore', { goodsId, goodsNum })
      await changeCount(goodsId, goodsNum, goodsSkuId)
    },
    // 删除商品项
    async deleteAction (context) {
      const selCartList = context.getters.selCartList
      const cartIds = selCartList.map(e => e.id)
      const res = await deleteItem(cartIds)
      console.log(res)
      Toast('删除成功！')

      // 本地再次调取后台数据
      context.dispatch('getCartAction')
    }
  },
  getters: {
    // 选中的商品项：
    selCartList (state) {
      return state.cartList.filter(e => e.isChecked)
    },

    // 选中的总数：
    selCount (state, getters) {
      return getters.selCartList.reduce((c, e) => c + e.goods_num, 0)
    },

    // 选中的总价：
    selPrice (state, getters) {
      return getters.selCartList.reduce((c, e) => c + e.goods_num * e.goods.goods_price_min, 0).toFixed(2)
    },

    // 是否全选
    isAllChecked (state) {
      return state.cartList.every(e => e.isChecked === true)
    }
  }
}
